	module.exports = {"data":{"markdownRemark":{"html":"<p>政治的供給に関する記事を完成させる。</p>\n<p>政府と政治体制が徐々に経済運営から撤退した後、政府の機能はマクロと間接的な管理に集中していった。このように、社会全体はより大きなシステムのようになります。このシステムでは、すべてを制御する権限はなく、一連のチャネルを介する権限はありません。すべてのサブシステムは1つの相互作用にあります。市場経済の原則はこのようなものかもしれません。政治システムが過去の権力とチャネルを失った後、どのようにして社会の経済発展を制御するかは、新しい方法を持たなければならない。全体的な視点から見ると、この新しいアプローチは、大規模なシステムにおいて、政治の供給、つまりインプットを増加させることである。政治的インプットを通じて理想的な成果を期待している。したがって、政治的供給は、政府と政治体制が市場経済の条件下で社会経済開発全体を規制する主要な方法となるであろう。</p>\n<p>クラス。第一は、行政管理と孔子論の議論である。 2つ目は現代の西洋政治思想であり、「ザラトゥストラは言っているようです」と商業出版はそのタイトルを「スルジの引用」に翻訳しましたが、そのような大きな違いはわかりません。</p>\n<p>若い人たちは孔子の考えを理解していませんが、中国のような文化的雰囲気の中に儒教思想が存在する理由は分かりません。それはイデオロギーだけでなく、生き方の反映です。中国社会では、人々は常に、外界からではなく、人々の内なる世界と外界との協調を強調してきました。すべての秩序、政治的なもの、経済的なもの、社会的なもの、そして最後に精神的な秩序。精神的な秩序の形成の前に、外部の秩序は強くない。これは儒教の最も貴重な部分です。世界は無くてはいけない</p>\n<p>秩序は倫理秩序なしにはできないが、儒教はこの点を強調している。問題は、オーダーを構築するために使用されるコンテンツの種類ではなく、オーダー自体です。コンテンツは常に歴史の発展と共に発展しています。</p>\n<p>同級生の一人がニーチェについて話し合い、ニーチェは人の内面世界に注目し、人生の意味に注意を払い、現代科学技術による人間性の喪失に注意を払ったと語った。後にフロイト、サルトル、マルクーゼはこれらの3つの面でニーチェの心配を発展させました。本質的には、ニーチェは人間の本性と人間の欲望を華やかにしたいと考えています。近代文明は人間の本能を制限していると信じているので、「スーパーマン」が存在しなければなりません。ニーチェ自身は彼が \"スーパーマン\"だと思っていたが、最終的に彼はあまり考えすぎて精神分裂病を患っていた。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0046.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0046.md","frontmatter":{"title":"3月1日 火曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0046.md absPath of file >>> MarkdownRemark"}}

